<template>
    <div class="XLogin pa-3 fill-width">
        <v-card :flat="scheme.flat" :max-width="scheme.width" class="mx-auto">
            <v-card-text class="text-center">
                <slot v-if="!!this.$slots['avatar']" name="avatar"></slot>

                <v-avatar v-else color="tree" size="80" class="mt-3">
                    <v-img src="@/assets/imgs/user.png" max-width="160"></v-img>
                </v-avatar>

                <div v-if="!this.$slots['title']" class="text-center mt-4 mb-8">
                    <div class="text-subtitle-1"> {{ $t(scheme.title) }} </div>
                    <div class="text-subtitle-2 mt-1"> {{ $t(scheme.subTitle) }} </div>
                </div>

                <slot v-else name="title"></slot>

                <v-form @submit.prevent="login" v-model="scheme.form.valid" class="x-login-form">
                    <v-text-field ref="usernameRef" v-model="scheme.form.fields.username.value" :label="$t('login.username.label')" :dense="scheme.dense" :disabled="scheme.loading" :placeholder="$t('login.username.placeholder')" :rules="scheme.form.fields.username.rules" outlined :color="scheme.loginTheme">
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-text-field v-model="scheme.form.fields.password.value" :label="$t('login.pwd.label')" outlined :dense="scheme.dense" :type="scheme.form.fields.password.showValue ? 'text' : 'password'" :disabled="scheme.loading" placeholder="********" :rules="scheme.form.fields.password.rules" :append-icon="scheme.pwdToggle ? (scheme.form.fields.password.showValue ? 'mdi-eye-off-outline' : 'mdi-eye-outline') : ''" @click:append="scheme.form.fields.password.showValue = !scheme.form.fields.password.showValue" :color="scheme.loginTheme">
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-select v-if="scheme.showEnv" :label="$t('login.env.label')" v-model="scheme.form.fields.environment.value" :items="scheme.environments" return-object outlined :disabled="scheme.loading" :dense="scheme.dense" :color="scheme.loginTheme"></v-select>

                    <v-btn v-if="scheme.forgotPwd" @click.prevent="resetPwdClicked" :disabled="scheme.loading" text small :color="scheme.loginTheme"> {{ $t('login.recovery.btn') }} </v-btn>

                    <v-btn type="submit" :disabled="!scheme.form.valid || scheme.loading" :loading="scheme.loading" :color="scheme.loginTheme" block class="mt-4"> {{ $t('login.enter.btn') }} </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
import { validations, recursiveMerge } from '../../commons';

export default {
    name: 'XLogin',
    props: [
        'title',
        'subTitle',
        'version',
        'pwdToggle',
        'showEnv',
        'environments',
        'forgotPwd',
        'width',
        'flat',
        'loginTheme',
        'dark',
        'dense',
        'loading',
        'options',
    ],
    data() {
        return {
            scheme: {
                ...this.$props,
                ...this.options,

                form: {
                    valid: false,
                    loading: false,
                    fields: {
                        username: {
                            value: 'MASTER',
                            visible: true,
                            disabled: false,
                            loading: false,
                            showValue: false,
                            rules: [
                                validations.required,
                                // validations.email
                            ]
                        },
                        password: {
                            value: '123456',
                            visible: true,
                            disabled: false,
                            loading: false,
                            showValue: false,
                            rules: [
                                validations.required,
                            ]
                        },
                        environment: {
                            value: { value: 'P', text: 'PRODUÇÃO' },
                            visible: true,
                            disabled: false,
                            loading: false,
                            showValue: false,
                            rules: [
                                validations.required,
                            ]
                        }
                    },
                },
            },
        }
    },
    methods: {
        async login() {
            try {
                this.$emit('login', this.scheme.form);

                if (this.scheme.onLogin) {
                    this.scheme.onLogin(this.scheme.form);
                }
            } catch (err) {
                this.$emit('login', err);
                if (this.scheme.onLogin) {
                    this.scheme.onLogin(err);
                }
            }
        },

        async logout() {

        },

        resetPwdClicked() {
            this.$emit('step', { id: 1, data: null });
        },
    },
    created() {

    },
    mounted() {
        setTimeout(() => this.$refs.usernameRef.focus());
    },
    watch: {
        'options': {
            deep: true,
            handler(newVal) {
                this.scheme = {
                    ...this.scheme,
                    ...this.$props,
                    ...newVal
                };
            },
        }
    },
}
</script>

