<template>
    <div class="XResetPwd pa-3 mx-auto">
        <v-card v-if="!completed" :flat="flat" :max-width="width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mb-4 mt-3">
                    <v-img src="@/assets/imgs/user.png" max-width="160"></v-img>
                </v-avatar>

                <p class="text-h5 font-weight-light text-center mb-2"> MARIA </p>

                <p class="text-center mb-6"> {{ $t('login.recovery.change') }} </p>

                <v-form v-model="rules.isValid.frmResetPwd">
                    <v-text-field v-model="passwordRecovery.newPwd" :type="visible.pwd1 ? 'text' : 'password'" label="Nova Senha" placeholder="Informe sua nova senha." :rules="[rules.required]" :append-icon="visible.pwd1 ? 'mdi-eye-off-outline' : 'mdi-eye-outline'" @click:append="visible.pwd1 = !visible.pwd1" :dense="dense" :color="loginTheme" class="mb-3" outlined></v-text-field>

                    <v-text-field v-model="passwordRecovery.confirmPwd" :type="visible.pwd2 ? 'text' : 'password'" label="Confirmação da Senha" placeholder="Confirme sua nova senha." :rules="[rules.required, rules.confirmPwd]" :append-icon="visible.pwd2 ? 'mdi-eye-off-outline' : 'mdi-eye-outline'" @click:append="visible.pwd2 = !visible.pwd2" :dense="dense" :color="loginTheme" outlined></v-text-field>

                    <v-btn @click="resetPassword" :disabled="!rules.isValid.frmResetPwd" block :color="loginTheme" :dark="dark" class="mt-2"> CONFIRMAR </v-btn>

                    <v-btn @click.prevent="backToLogin" text small block :color="loginTheme" class="mt-4"> RETORNAR </v-btn>
                </v-form>
            </v-card-text>
        </v-card>

        <v-card v-else :flat="flat" :max-width="width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mb-4 mt-3">
                    <v-img src="@/assets/imgs/user.png" max-width="160"></v-img>
                </v-avatar>

                <p class="text-h5 font-weight-light text-center mb-4"> MARIA </p>

                <p class="text-center mb-6"> {{ $t('login.recovery.completed.title') }} </p>
                <p class="text-center mb-6"> {{ $t('login.recovery.completed.subtitle') }} </p>

                <v-btn @click.prevent="backToLogin" block :color="loginTheme" :dark="dark"> RETORNAR </v-btn>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XResetPwd',
    props: [
        'pwdToggle',
        'width',
        'flat',
        'loginTheme',
        'dark',
        'dense'
    ],
    data() {
        return {
            completed: false,
            newPwdToggle: false,
            confirmPwdToggle: false,

            visible: {
                pwd1: false,
                pwd2: false,
            },

            passwordRecovery: {
                code: '123456',
                email: 'test@test.com',
                newPwd: '123456789',
                confirmPwd: '123456789'
            },

            rules: {
                isValid: {
                    frmResetPwd: false,
                },
                required: (value) => {
                    return !!value || 'Esse campo é obrigatório!';
                },
                email: (value) => {
                    const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    return pattern.test(value) || 'E-mail inválido!';
                },
                confirmPwd: (value) => {
                    if (value !== this.passwordRecovery.newPwd) {
                        return 'As senha informadas devem ser iguais!';
                    }
                    return true;
                },
            },
        }
    },
    methods: {
        backToLogin() {
            this.$emit('step', { id: 0, data: {} });
        },

        resetPassword() {
            this.completed = true;
            this.$emit('step', { id: 3, data: this.passwordRecovery });
        },
    },
}
</script>

<style scoped>
.XResetPwd{
    max-width: 350px;
}
</style>
