<template>
    <div class="XResetPwdSendMail pa-3 mx-auto">
        <v-card :flat="flat" :max-width="width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mb-3 mt-3">
                    <v-icon size="50">mdi-email-arrow-right</v-icon>
                </v-avatar>

                <p class="mb-6 text-center"> {{ $t('login.recovery.email') }} </p>

                <v-form v-model="rules.isValid.frmPwdRecovery">
                    <v-text-field ref="emailRef" v-model="passwordRecovery.email" label="E-mail" placeholder="Informe seu email cadastrado." :rules="[rules.required, rules.email]" :dense="dense" :color="loginTheme" outlined></v-text-field>

                    <v-btn @click.prevent="sendMailPwdRecover" :disabled="!rules.isValid.frmPwdRecovery" block :color="loginTheme" :dark="dark" class="mt-2"> CONFIRMAR </v-btn>

                    <v-btn @click.prevent="backToLogin" text small block :color="loginTheme" class="mt-4"> RETORNAR </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XResetPwdSendMail',
    props: [
        'width',
        'flat',
        'loginTheme',
        'dark',
        'dense'
    ],
    data() {
        return {
            passwordRecovery: {
                code: '123456',
                email: 'test@test.com',
                newPwd: '123456789',
                confirmPwd: '123456789'
            },

            rules: {
                isValid: {
                    frmPwdRecovery: false,
                },
                required: (value) => {
                    return !!value || 'Esse campo é obrigatório!';
                },
                email: (value) => {
                    const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                    return pattern.test(value) || 'E-mail inválido!';
                },
                confirmPwd: (value) => {
                    if (value !== this.passwordRecovery.newPwd) {
                        return 'As senha informadas devem ser iguais!';
                    }
                    return true;
                },
            },
        }
    },
    methods: {
        backToLogin() {
            this.$emit('step', { id: 0, data: null });
        },

        sendMailPwdRecover() {
            this.$emit('step', { id: 2, data: this.passwordRecovery });
        },
    },
}
</script>

<style scoped>
.XResetPwdSendMail{
    max-width: 350px;
}
</style>
