<template>
    <div :class="['x-authenticator full-page', $vuetify.theme.isDark ? 'theme--dark' : 'theme--light']">
        <v-row no-gutters class="fill-height">
            <v-col lg="8" v-if="scheme.splash" :class="['x-left-panel d-none d-lg-flex align-center justify-center', scheme.splashTheme ? scheme.splashTheme : '']">
                <x-splash-screen>
                    <slot name="splash"></slot>
                </x-splash-screen>
            </v-col>

            <v-col :lg="scheme.splash ? 4 : 12" :class="['x-right-panel d-flex flex-column align-center justify-center', scheme.loginAreaTheme ? scheme.loginAreaTheme : '']">
                <div class="mt-2">
                    <span :class="['text-caption', {'white--text': dark || $vuetify.theme.isDark }]"> {{ $t('version') }}: {{ scheme.version }} </span>
                </div>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-login v-if="step === 0" @step="setStep" @login="$emit('login', $event)" :options="scheme" v-bind="$props">
                        <template slot="title">
                            <slot name="title"></slot>
                        </template>
                    </x-login>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-reset-pwd-send-mail v-if="step === 1" @step="setStep" v-bind="$props" class="fill-width"></x-reset-pwd-send-mail>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-reset-pwd-check-code v-if="step === 2" @step="setStep" v-bind="$props" class="fill-width"></x-reset-pwd-check-code>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-reset-pwd v-if="step === 3" @step="setStep" v-bind="$props" class="fill-width"></x-reset-pwd>
                </v-slide-y-reverse-transition>

                <div v-if="scheme.translate" class="py-2">
                    <v-btn v-for="locale of $i18n.availableLocales" :key="locale" @click="$i18n.setLocale(locale)" icon color="primary" class="mx-1">
                        <v-img :src="require(`@/assets/imgs/flags/${locale.toLowerCase()}.svg`)" max-width="32"></v-img>
                    </v-btn>
                </div>

            </v-col>
        </v-row>
    </div>
</template>

<script>
import { validations, recursiveMerge, isNotNull } from '../../commons';
import XResetPwdCheckCode from './ResetPwdCheckCode.vue';
import XResetPwdSendMail from './ResetPwdSendMail.vue';
import XSplashScreen from './SplashScreen.vue';
import XResetPwd from './ResetPwd.vue';
import XLogin from './Login.vue';

export default {
    name: 'Authenticator',
    components: {
        XSplashScreen,
        XLogin,
        XResetPwdSendMail,
        XResetPwdCheckCode,
        XResetPwd
    },
    props: {
        splash: {
            type: Boolean,
            default: true
        },
        splashTheme: {
            type: String,
            default: 'one'
        },
        loginAreaTheme: {
            type: String,
            default: 'tree'
        },
        loginTheme: {
            type: String,
            default: 'primary'
        },
        dark: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: 'login.title'
        },
        subTitle: {
            type: String,
            default: 'login.subtitle'
        },
        version: {
            type: String,
            default: '0.0.1'
        },
        pwdToggle: {
            type: Boolean,
            default: true
        },
        forgotPwd: {
            type: Boolean,
            default: true
        },
        showEnv: {
            type: Boolean,
            default: true
        },
        environments: {
            type: Array,
            default: () => [
                { value: 'P', text: 'PRODUÇÃO' },
                { value: 'V', text: 'VALIDAÇÃO' },
                { value: 'T', text: 'TESTE' },
                { value: 'H', text: 'HOMOLOGAÇÃO' }
            ]
        },
        flat: {
            type: Boolean,
            default: false
        },
        dense: {
            type: Boolean,
            default: true
        },
        translate: {
            type: Boolean,
            default: true
        },
        width: {
            type: String,
            default: '380px'
        },
        loading: {
            type: Boolean,
            default: false
        },
        options: {
            type: Object
        },
    },
    data() {
        return {
            step: 0,
            locales: [],

            scheme: {
                ...this.$props,
                ...this.options,
            },
        }
    },
    methods: {
        setStep(step) {
            console.log(step);
            this.step = step.id;
        },
    },
    mounted() {
        // console.log('>>>', this.$i18n.availableLocales);
        // this.schemeData = recursiveMerge(this.schemeData, this.scheme);
        // console.log('>>>', this.$props);
    },
    watch: {
        'options': {
            deep: true,
            handler(newVal) {
                this.scheme = {
                    ...this.scheme,
                    ...this.$props,
                    ...newVal
                };
            },
        }
    },
}
</script>

<style scoped>
.x-authenticator.theme--light .x-left-panel{
    border-right: 1px solid rgba(0, 0, 0, 0.12) !important;
}
.x-authenticator.theme--dark .x-left-panel{
    border-right: 1px solid rgba(255, 255, 255, 0.12) !important;
}
</style>
